import { request } from 'utils';

const prefix = '/api/categories';

export const getDormitoryRooms = ({
  dormId,
  floorId,
  roomNo,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Dormitory/${dormId}/DormitoryFloor/${floorId}/DormitoryRoom`,
    method: 'GET',
    params: { dormId, floorId, roomNo, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createDormitoryRoom = ({ dormId, floorId, roomNo, size, device, feePerPerson } = {}) =>
  request({
    prefix,
    url: `/Dormitory/${dormId}/DormitoryFloor/${floorId}/DormitoryRoom`,
    method: 'POST',
    params: {},
    data: { roomNo, size, device, feePerPerson },
  });

export const getDormitoryRoomById = ({ dormId, floorId, roomId } = {}) =>
  request({
    prefix,
    url: `/Dormitory/${dormId}/DormitoryFloor/${floorId}/DormitoryRoom/${roomId}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const updateDormitoryRoom = ({
  dormId,
  floorId,
  roomId,
  roomNo,
  size,
  device,
  feePerPerson,
} = {}) =>
  request({
    prefix,
    url: `/Dormitory/${dormId}/DormitoryFloor/${floorId}/DormitoryRoom/${roomId}`,
    method: 'PUT',
    params: {},
    data: { roomNo, size, device, feePerPerson },
  });

export const deleteDormitoryRoom = ({ dormId, floorId, roomId } = {}) =>
  request({
    prefix,
    url: `/Dormitory/${dormId}/DormitoryFloor/${floorId}/DormitoryRoom/${roomId}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



